import { State } from "../../model/State";
export class FileDropdownMenu extends createjs.EventDispatcher {
    //----------protected-------
    //=============================================
    // constructor
    //=============================================
    constructor(state) {
        super();
        //=============================================
        // /
        //=============================================
        //----------public----------
        this.EVENT_OPEN_FILE_DROPDOWN = "event open file dropdown";
        this.EVENT_CLOSE_FILE_DROPDOWN = "event close file dropdown";
        this.EVENT_SELECT_MENU_FILE_DROPDOWN = "event select menu file dropdown";
        //=============================================
        // event handler
        //=============================================
        this._onClickHandler = (e) => {
            let target = e.currentTarget;
            let mode;
            if (target.id == "fileNewMenu") {
                mode = State.FILE_NEW;
            }
            else if (target.id == "fileLoadMenu") {
                mode = State.FILE_LOAD_JSON_FROM_LOCAL;
            }
            else if (target.id == "fileSaveMenu") {
                mode = State.FILE_SAVE_JSON_TO_LOCAL;
            }
            else if (target.id == "publishSvgToLocal") {
                mode = State.FILE_SAVE_SVG_TO_LOCAL;
            }
            this._state.setCurrent(mode);
            this.dispatchEvent(new createjs.Event(this.EVENT_SELECT_MENU_FILE_DROPDOWN, true, true));
            e.preventDefault();
        };
        this._state = state;
        //----------------------
        // 
        //----------------------
        let fdd = document.querySelector('#fileDropdown');
        //
        fdd.addEventListener("show.bs.dropdown", (e) => {
            this.dispatchEvent(new createjs.Event(this.EVENT_OPEN_FILE_DROPDOWN, true, true));
        });
        //
        fdd.addEventListener("hide.bs.dropdown", (e) => {
            //this.reset();
            this.dispatchEvent(new createjs.Event(this.EVENT_CLOSE_FILE_DROPDOWN, true, true));
        });
        //----------------------
        // 
        //----------------------
        //
        let newLink = document.querySelector('#fileDropdown a#fileNewMenu');
        //JSON
        let loadJsonFromLocalLink = document.querySelector('#fileDropdown a#fileLoadMenu');
        //JSON
        let saveJsonToLocalLink = document.querySelector('#fileDropdown a#fileSaveMenu');
        //SVG
        let publishSvgToLocalLink = document.querySelector('#fileDropdown #filePublishMenu a#publishSvgToLocal');
        this._anchorList = [
            newLink,
            loadJsonFromLocalLink,
            saveJsonToLocalLink,
            publishSvgToLocalLink
        ];
        for (let anchor of this._anchorList) {
            anchor.addEventListener('click', this._onClickHandler);
        }
    }
}
